<template>
  <div>
    <button class="language-switcher" @click="toggleVisibility">{{ $i18n.locale.toUpperCase() }}</button>

    <ul class="language-dropdown" v-on-clickaway="hideDropdown" v-if="isVisible" ref="dropdown">
      <li>
        <a href="#" @click.prevent="setLocale('de')">
          <span>Deutsch</span>
        </a>
      </li>
      <li>
        <a href="#" @click.prevent="setLocale('en')">
          <span>English</span>
        </a>
      </li>
      <li>
        <a href="#" @click.prevent="setLocale('fa')">
          <span>Farsi</span>
        </a>
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  data() {
    return {
      isVisible: false
    };
  },

  methods: {
    toggleVisibility() {
      this.isVisible = !this.isVisible;
    },

    hideDropdown() {
      this.isVisible = false;
    },

    setLocale(locale) {
      this.$i18n.locale = locale;
      this.$router.push({
        params: { lang: locale }
      });
      this.hideDropdown();
    }
  }
};
</script>

<style lang="scss">
.switcher {
  display: flex;
  justify-content: flex-start;
}
.language-switcher {
  right: 100px;
  position: absolute;
}
.language-dropdown {
  position: absolute;
  z-index: 50;
  right: 77px;
  margin-top: 30px;
}
</style>
